'use client';

import { Navbar } from '../components/Navbar';
import { Footer } from '../components/Footer';
import { HomePage } from '../pages/HomePage';

export default function Home() {
  // For now, we'll pass a mock navigate function since Next.js handles routing differently
  const handleNavigate = (page: 'home' | 'services' | 'careers' | 'contact') => {
    if (page === 'services') {
      window.location.href = '/services';
    } else if (page === 'careers') {
      window.location.href = '/careers';
    } else if (page === 'contact') {
      window.location.href = '/contact';
    }
    // Home is the current page, so no navigation needed
  };

  return (
    <div className="min-h-screen flex flex-col bg-white">
      <Navbar activePage="home" onNavigate={handleNavigate} />
      <div className="flex-1">
        <HomePage onNavigate={handleNavigate} />
      </div>
      <Footer onNavigate={handleNavigate} />
    </div>
  );
}